

import { Direction, Directionality } from '@angular/cdk/bidi';
import { BooleanInput, NumberInput } from '@angular/cdk/coercion';
import { Platform } from '@angular/cdk/platform';
import { DOCUMENT } from '@angular/common';
import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EmbeddedViewRef,
  EventEmitter,
  Inject,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  Renderer2,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewContainerRef,
  ViewEncapsulation
} from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { cancelRequestAnimationFrame, reqAnimFrame } from 'tds-ui/core/animation';
import { TDSConfigKey, TDSConfigService, TDSTSType, WithConfig } from 'tds-ui/core/config';
import { TDSResizeService } from 'tds-ui/core/services';
import { isStyleSupport, measure } from 'tds-ui/core/util';
import { TDSI18nService, TDSTextI18nInterface } from 'tds-ui/i18n';
import { InputBoolean, InputNumber, TDSSafeAny } from 'tds-ui/shared/utility';


import { TDSTextCopyComponent } from './text-copy.component';
import { TDSTextEditComponent } from './text-edit.component';

const TDS_CONFIG_MODULE_NAME: TDSConfigKey = 'typography';
const EXPAND_ELEMENT_CLASSNAME = 'tds-typography-expand';

@Component({
  selector: `
  tds-typography,
  [tds-typography],
  p[tds-paragraph],
  span[tds-text],
  h1[tds-title], h2[tds-title], h3[tds-title], h4[tds-title]
  `,
  exportAs: 'tdsTypography',
  template: `
    <ng-template #contentTemplate let-content="content">
      <ng-content *ngIf="!content"></ng-content>
      {{ content }}    
      <a *ngIf="expandable && !isEllipsis" class="inline-block" (click)="onCollapse($event)">      
      <ng-container *tdsStringTemplateOutlet="contentCollapse">
      <span
        class="tds-typography-collapse text-info-500 cursor-pointer text-body-2">
        {{ contentCollapse }}
      </span>
     
      </ng-container>
    </a>
    </ng-template>
    <ng-container *ngIf="!editing">
      <ng-container
        *ngIf="
          expanded ||
            (!hasOperationsWithEllipsis && ellipsisRows === 1 && !hasEllipsisObservers) ||
            canCssEllipsis ||
            (suffix && expanded);
          else jsEllipsis
        "
      >
        <ng-template
          [ngTemplateOutlet]="contentTemplate"
          [ngTemplateOutletContext]="{ content: content }"
        ></ng-template>
        <ng-container *ngIf="suffix">{{ suffix }}</ng-container>
      </ng-container>
      <ng-template #jsEllipsis>
        <span #ellipsisContainer></span>
        <ng-container *ngIf="isEllipsis">{{ ellipsisStr }}</ng-container>
        <ng-container *ngIf="suffix">{{ suffix }}</ng-container>
        <a #expandable *ngIf="expandable && isEllipsis" class="tds-typography-expand text-info-500 cursor-pointer text-body-2" (click)="onExpand($event)">
          {{ locale?.expand }}
        </a>
      </ng-template>
    </ng-container>

    <tds-text-edit
      *ngIf="editable"
      [text]="content"
      [icon]="editIcon"
      [tooltip]="editTooltip"
      (endEditing)="onEndEditing($event)"
      (startEditing)="onStartEditing()"
     
    ></tds-text-edit>

    <tds-text-copy
      *ngIf="copyable && !editing"
      [text]="onCopyText"
      [tooltips]="copyTooltips"
      [icons]="copyIcons"
      (textCopy)="onTextCopy($event)"
    ></tds-text-copy>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  host: {
    '[class.tds-typography]': '!editing',
    '[class.break-words]': '!editing',
    '[class.tds-typography-rtl]': 'dir === "rtl"',
    '[class.tds-typography-edit-content]': 'editing',
    '[class.text-neutral-1-400]': 'color === "secondary"',
    '[class.text-warning-400]': 'color === "warning"',
    '[class.text-error-400]': 'color === "danger"',
    '[class.text-success-400]': 'color === "success"',
    '[class.tds-typography-disabled]': 'disabled',
    '[class.tds-typography-ellipsis]': 'ellipsis && !expanded',

    '[class.tds-typography-single-line]': 'ellipsis && ellipsisRows === 1',
    '[class.whitespace-nowrap]': 'ellipsis && ellipsisRows === 1',

    '[class.tds-typography-ellipsis-single-line]': 'canCssEllipsis && ellipsisRows === 1',
    '[class.overflow-hidden]': 'canCssEllipsis && ellipsisRows === 1',
    '[class.overflow-ellipsis]': 'canCssEllipsis && ellipsisRows === 1',

    '[class.tds-typography-ellipsis-multiple-line]': 'canCssEllipsis && ellipsisRows > 1',
    '[style.-webkit-line-clamp]': '(canCssEllipsis && ellipsisRows > 1) ? ellipsisRows : null'
  }
})
export class TDSTypographyComponent implements OnInit, AfterViewInit, OnDestroy, OnChanges {
  readonly _tdsModuleName: TDSConfigKey = TDS_CONFIG_MODULE_NAME;

  static ngAcceptInputType_copyable: BooleanInput;
  static ngAcceptInputType_editable: BooleanInput;
  static ngAcceptInputType_disabled: BooleanInput;
  static ngAcceptInputType_expandable: BooleanInput;
  static ngAcceptInputType_ellipsis: BooleanInput;
  static ngAcceptInputType_ellipsisRows: NumberInput;
  @Input() @InputBoolean() copyable = false;
  @Input() @InputBoolean() editable = false;
  @Input() @InputBoolean() disabled = false;
  @Input() @InputBoolean() expandable = false;
  @Input() @InputBoolean() ellipsis = false;
  @Input() @WithConfig() copyTooltips?: [TDSTSType, TDSTSType] | null = undefined;
  @Input() @WithConfig() copyIcons: [TDSTSType, TDSTSType] = ['tdsi-copy-fill', 'tdsi-tick-fill'];
  @Input() @WithConfig() editTooltip?: null | TDSTSType = undefined;
  @Input() @WithConfig() editIcon: TDSTSType = 'tdsi-edit-fill';
  @Input() content?: string;
  @Input() @WithConfig() @InputNumber() ellipsisRows: number = 1;
  @Input() color: 'secondary' | 'warning' | 'error' | 'success' | undefined;
  @Input() copyText: string | undefined;
  @Input() suffix: string | undefined;
  @Input() contentCollapse!: string | TemplateRef<TDSSafeAny>;
  @Output() readonly contentChange = new EventEmitter<string>();
  @Output() readonly copy = new EventEmitter<string>();
  @Output() readonly expandChange = new EventEmitter<void>();
  // This is not a two-way binding output with {@link ellipsis}
  @Output() readonly ellipsisChange = new EventEmitter<boolean>();

  @ViewChild(TDSTextEditComponent, { static: false }) textEditRef?: TDSTextEditComponent;
  @ViewChild(TDSTextCopyComponent, { static: false }) textCopyRef?: TDSTextCopyComponent;
  @ViewChild('ellipsisContainer', { static: false }) ellipsisContainer?: ElementRef<HTMLSpanElement>;
  @ViewChild('expandable', { static: false }) expandableBtn?: ElementRef<HTMLSpanElement>;
  @ViewChild('contentTemplate', { static: false }) contentTemplate?: TemplateRef<{ content: string }>;

  locale!: TDSTextI18nInterface;
  document: Document;
  expandableBtnElementCache: HTMLElement | null = null;
  editing = false;
  ellipsisText: string | undefined;
  cssEllipsis: boolean = false;
  isEllipsis: boolean = true;
  expanded: boolean = false;
  ellipsisStr = '...';
  dir: Direction = 'ltr';

  get hasEllipsisObservers(): boolean {
    return this.ellipsisChange.observers.length > 0;
  }

  get canCssEllipsis(): boolean {
    return this.ellipsis && this.cssEllipsis && !this.expanded && !this.hasEllipsisObservers;
  }

  get hasOperationsWithEllipsis(): boolean {
    return (this.copyable || this.editable || this.expandable) && this.ellipsis;
  }

  private viewInit = false;
  private rfaId: number = -1;
  private destroy$ = new Subject<void>();
  private windowResizeSubscription = Subscription.EMPTY;
  get onCopyText(): string {
    return (typeof this.copyText === 'string' ? this.copyText : this.content)!;
  }

  constructor(
    public tdsConfigService: TDSConfigService,
    private host: ElementRef<HTMLElement>,
    private cdr: ChangeDetectorRef,
    private viewContainerRef: ViewContainerRef,
    private renderer: Renderer2,
    private platform: Platform,
    private i18n: TDSI18nService,
    @Inject(DOCUMENT) document: TDSSafeAny,
    private resizeService: TDSResizeService,
    @Optional() private directionality: Directionality
  ) {
    this.document = document;
  }

  onTextCopy(text: string): void {
    this.copy.emit(text);
  }

  onStartEditing(): void {
    this.editing = true;
  }

  onEndEditing(text: string): void {
    this.editing = false;
    this.contentChange.emit(text);
    if (this.content === text) {
      this.renderOnNextFrame();
    }
    this.cdr.markForCheck();
  }

  onExpand(event: MouseEvent | null): void {
    if(event)
    {
      event.preventDefault();
      event.stopImmediatePropagation();
    }
    this.isEllipsis = false;
    this.expanded = true;
    this.expandChange.emit();
    this.ellipsisChange.emit(false);
  }

  canUseCSSEllipsis(): boolean {
    if (this.editable || this.copyable || this.expandable || this.suffix) {
      return false;
    }
    // make sure {@link ellipsisChange} works, will force use JS to calculations
    if (this.hasEllipsisObservers) {
      return false;
    }
    if (this.ellipsisRows === 1) {
      return isStyleSupport('textOverflow');
    } else {
      return isStyleSupport('webkitLineClamp');
    }
  }

  renderOnNextFrame(): void {
    cancelRequestAnimationFrame(this.rfaId);
    if (!this.viewInit || !this.ellipsis || this.ellipsisRows < 0 || this.expanded || !this.platform.isBrowser) {
      return;
    }
    this.rfaId = reqAnimFrame(() => {
      this.syncEllipsis();
    });
  }

  getOriginContentViewRef(): { viewRef: EmbeddedViewRef<{ content: string }>; removeView(): void } {
    const viewRef = this.viewContainerRef.createEmbeddedView<{ content: string }>(this.contentTemplate!, {
      content: this.content!
    });
    viewRef.detectChanges();
    return {
      viewRef,
      removeView: () => {
        this.viewContainerRef.remove(this.viewContainerRef.indexOf(viewRef));
      }
    };
  }

  syncEllipsis(): void {
    if (this.cssEllipsis) {
      return;
    }
    const { viewRef, removeView } = this.getOriginContentViewRef();
    const fixedNodes = [this.textCopyRef, this.textEditRef]
      .filter(e => e && e.nativeElement)
      .map(e => e!.nativeElement);
    const expandableBtnElement = this.getExpandableBtnElement();
    if (expandableBtnElement) {
      fixedNodes.push(expandableBtnElement);
    }
    const { contentNodes, text, ellipsis } = measure(
      this.host.nativeElement,
      this.ellipsisRows,
      viewRef.rootNodes,
      fixedNodes,
      this.ellipsisStr,
      this.suffix
    );

    removeView();

    this.ellipsisText = text;
    if (ellipsis !== this.isEllipsis) {
      this.isEllipsis = ellipsis;
      this.ellipsisChange.emit(ellipsis);
    }
    const ellipsisContainerNativeElement = this.ellipsisContainer!.nativeElement;
    while (ellipsisContainerNativeElement.firstChild) {
      this.renderer.removeChild(ellipsisContainerNativeElement, ellipsisContainerNativeElement.firstChild);
    }
    contentNodes.forEach(n => {
      this.renderer.appendChild(ellipsisContainerNativeElement, n.cloneNode(true));
    });
    this.cdr.markForCheck();
  }

  // Need to create the element for calculation size before view init
  private getExpandableBtnElement(): HTMLElement | null {
    if (this.expandable) {
      const expandText = this.locale ? this.locale.expand : '';
      const cache = this.expandableBtnElementCache;
      if (!cache || cache.innerText === expandText) {
        const el = this.document.createElement('a');
        el.className = EXPAND_ELEMENT_CLASSNAME;
        el.innerText = expandText;
        this.expandableBtnElementCache = el;
      }
      return this.expandableBtnElementCache;
    } else {
      this.expandableBtnElementCache = null;
      return null;
    }
  }

  private renderAndSubscribeWindowResize(): void {
    if (this.platform.isBrowser) {
      this.windowResizeSubscription.unsubscribe();
      this.cssEllipsis = this.canUseCSSEllipsis();
      this.renderOnNextFrame();
      this.windowResizeSubscription = this.resizeService
        .subscribe()
        .pipe(takeUntil(this.destroy$))
        .subscribe(() => this.renderOnNextFrame());
    }
  }

  ngOnInit(): void {
    this.i18n.localeChange.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.locale = this.i18n.getLocaleData('Text');
      this.cdr.markForCheck();
    });

    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.cdr.detectChanges();
    });

    this.dir = this.directionality.value;
  }

  ngAfterViewInit(): void {
    this.viewInit = true;
    this.renderAndSubscribeWindowResize();
  }

  ngOnChanges(changes: SimpleChanges): void {
    const { copyable, editable, expandable, ellipsis, content, ellipsisRows, suffix } = changes;
    if (copyable || editable || expandable || ellipsis || content || ellipsisRows || suffix) {
      if (this.ellipsis) {
        if (this.expanded) {
          this.windowResizeSubscription.unsubscribe();
        } else {
          this.renderAndSubscribeWindowResize();
        }

      }
    }
  }
  onCollapse(event:MouseEvent | null) {
    if(event)
    {
      event.preventDefault();
      event.stopImmediatePropagation();
    }
  
    if (this.expanded && !this.isEllipsis) {
      this.expanded = false;
      this.isEllipsis = true;
      this.ellipsis = true;
      this.ellipsisChange.emit(true);
      this.renderAndSubscribeWindowResize();
    }
  }
  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
    this.expandableBtnElementCache = null;
    this.windowResizeSubscription.unsubscribe();
  }
}
